import { Injectable } from '@angular/core';
import { Execution, ExecutionsList } from '@appwrite/common';
import { AppwriteException } from '../exceptions';
import { Payload } from '../types';
import { AppwriteService } from './';

@Injectable()
export class AppwriteFunctionsService<T = Execution> {

  constructor(
    private appwriteService: AppwriteService,
  ) {
  }


  /**
   * List Executions
   *
   * Get a list of all the current user function execution logs. You can use the
   * query params to filter your results. On admin mode, this endpoint will
   * return a list of all of the project's executions. [Learn more about
   * different API modes](/docs/admin).
   *
   * @param {string} functionId
   * @param {string} search
   * @param {number} limit
   * @param {number} offset
   * @param {string} orderType
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async listExecutions(functionId: string, search?: string, limit?: number, offset?: number, orderType?: string): Promise<ExecutionsList> {
    if (typeof functionId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "functionId"');
    }

    const path = '/functions/{functionId}/executions'.replace('{functionId}', functionId);
    const payload: Payload = {};

    if (typeof search !== 'undefined') {
      payload['search'] = search;
    }

    if (typeof limit !== 'undefined') {
      payload['limit'] = limit;
    }

    if (typeof offset !== 'undefined') {
      payload['offset'] = offset;
    }

    if (typeof orderType !== 'undefined') {
      payload['orderType'] = orderType;
    }

    return await this.appwriteService.call<ExecutionsList>('GET', path, { 'content-type': 'application/json', }, payload);
  }

  /**
   * Create Execution
   *
   * Trigger a function execution. The returned object will return you the
   * current execution status. You can ping the `Get Execution` endpoint to get
   * updates on the current execution status. Once this endpoint is called, your
   * function execution process will start asynchronously.
   *
   * @param {string} functionId
   * @param {string} data
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async createExecution(functionId: string, data?: string): Promise<T> {
    if (typeof functionId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "functionId"');
    }

    const path = '/functions/{functionId}/executions'.replace('{functionId}', functionId);
    const payload: Payload = {};

    if (typeof data !== 'undefined') {
      payload['data'] = data;
    }

    return await this.appwriteService.call<T>('POST', path, { 'content-type': 'application/json', }, payload);
  }

  /**
   * Get Execution
   *
   * Get a function execution log by its unique ID.
   *
   * @param {string} functionId
   * @param {string} executionId
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async getExecution(functionId: string, executionId: string): Promise<T> {
    if (typeof functionId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "functionId"');
    }

    if (typeof executionId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "executionId"');
    }

    const path = '/functions/{functionId}/executions/{executionId}'.replace('{functionId}', functionId)
      .replace('{executionId}', executionId);
    const payload: Payload = {};

    return await this.appwriteService.call<T>('GET', path, { 'content-type': 'application/json', }, payload);
  }

}
