import { HttpBackend, HttpClient, HttpResponse } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import URLParse from 'url-parse';
import { APPWRITE_LOCAL_STORAGE_PROVIDER_TOKEN, AppwriteOptions } from '../appwrite-angular.module';
import { AppwriteException } from '../exceptions';
import { APPWRITE_LOCATION_PROVIDER_TOKEN, APPWRITE_OPTIONS_TOKEN } from '../tokens';
import { Headers, HttpMethods, Payload } from '../types';
import { flatten } from '../util';

@Injectable({ providedIn: 'root' })
export class AppwriteService {
  config = {
    endpoint: 'https://appwrite.io/v1',
    project: '',
    jwt: '',
    locale: '',
  };
  headers: Headers = {
    'x-sdk-version': 'appwrite:web:3.2.0',
    'X-Appwrite-Response-Format': '0.9.0',
  };
  private http: HttpClient;

  constructor(
    @Inject(APPWRITE_OPTIONS_TOKEN) config: AppwriteOptions['config'],
    @Inject(APPWRITE_LOCAL_STORAGE_PROVIDER_TOKEN) public localStorageProvider,
    @Inject(APPWRITE_LOCATION_PROVIDER_TOKEN) public locationProvider,
    httpBackend: HttpBackend
  ) {
    this.http = new HttpClient(httpBackend);

    if (!config || !config.endpointUrl || !config.endpointUrl) {
      throw new Error('You are missing required config properties');
    }
    this.config.endpoint = config.endpointUrl;
    this.config.project = config.projectId;
    this.setEndpoint(config.endpointUrl).setProject(config.projectId);
  }

  setEndpoint(endpoint: string): this {
    this.config.endpoint = endpoint;
    return this;
  }

  setProject(value: string): this {
    this.headers['X-Appwrite-Project'] = value;
    this.config.project = value;
    return this;
  }

  setJWT(value: string): this {
    this.headers['X-Appwrite-JWT'] = value;
    this.config.jwt = value;
    return this;
  }

  setLocale(value: string): this {
    this.headers['X-Appwrite-Locale'] = value;
    this.config.locale = value;
    return this;
  }

  public async call<T>(method: HttpMethods, path: string, headers: Headers = {}, params: Payload = {}): Promise<T> {
    const url = new URLParse(this.config.endpoint + path);
    method = method.toUpperCase() as HttpMethods;
    headers = {
      ...headers,
      ...this.headers
    };
    const options: RequestInit = {
      method,
      headers,
      credentials: 'include'
    };
    if (typeof this.localStorageProvider !== 'undefined' && this.localStorageProvider) {
      headers['X-Fallback-Cookies'] = this.localStorageProvider.getItem('cookieFallback') ?? '';
    }

    if (method === 'GET') {
      url.set('query', flatten(params));
    } else {
      switch (headers['content-type']) {
        case 'application/json':
          options.body = JSON.stringify(params);
          break;

        case 'multipart/form-data':
          const formData = new FormData();

          for (const key in params) {
            if (Array.isArray(params[key])) {
              formData.append(key + '[]', params[key].join(','));
            } else {
              formData.append(key, params[key]);
            }
          }

          options.body = formData;
          delete headers['content-type'];
          break;
      }
    }

    try {

      const response: HttpResponse<T> = await this.http.request<T>(method, url.toString(), {
        ...options,
        headers: headers,
        // observe: 'response',
        observe: 'response' as 'response',
        // observe: 'response' as 'body',
      }).toPromise();
      let data: any | { message: string } = null;
      if (response.headers.get('content-type')?.includes('application/json')) {
        data = response.body;
      } else {
        data = {
          message: response.body
        };
      }

      if (400 <= response.status) { throw new AppwriteException(data?.message, response.status, data); }

      const cookieFallback = response.headers.get('X-Fallback-Cookies');

      if (typeof this.localStorageProvider !== 'undefined' && this.localStorageProvider && cookieFallback) {
        console.warn(
          'Appwrite is using localStorage for session management. Increase your security by adding a custom domain as your API endpoint.');
        this.localStorageProvider.setItem('cookieFallback', cookieFallback);
      }

      return data;
    } catch (e) {
      throw new AppwriteException(e.message);
    }
  }
}
