import { Injectable } from '@angular/core';
import { ContinentsList, Countries, CountriesList, CurrenciesList, LanguagesList, Locale, PhonesList } from '@appwrite/common';
import { AppwriteException } from '../exceptions';
import { Payload } from '../types';
import { AppwriteService } from './';


@Injectable()
export class AppwriteLocaleService {

  constructor(
    private appwriteService: AppwriteService,
  ) {
  }


  /**
   * Get User Locale
   *
   * Get the current user location based on IP. Returns an object with user
   * country code, country name, continent name, continent code, ip address and
   * suggested currency. You can use the locale header to get the data in a
   * supported language.
   *
   * ([IP Geolocation by DB-IP](https://db-ip.com))
   *
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async get(): Promise<Locale> {
    const path = '/locale';
    const payload: Payload = {};

    return await this.appwriteService.call<Locale>('GET', path, {
      'content-type': 'application/json',
    }, payload);
  }

  /**
   * List Continents
   *
   * List of all continents. You can use the locale header to get the data in a
   * supported language.
   *
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async getContinents(): Promise<ContinentsList> {
    const path = '/locale/continents';
    const payload: Payload = {};

    return await this.appwriteService.call<ContinentsList>('GET', path, {
      'content-type': 'application/json',
    }, payload);
  }

  /**
   * List Countries
   *
   * List of all countries. You can use the locale header to get the data in a
   * supported language.
   *
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async getCountries(): Promise<CountriesList> {
    const path = '/locale/countries';
    const payload: Payload = {};


    return await this.appwriteService.call<CountriesList>('GET', path, {
      'content-type': 'application/json',
    }, payload);
  }


  /**
   * List EU Countries
   *
   * List of all countries that are currently members of the EU. You can use the
   * locale header to get the data in a supported language.
   *
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async getCountriesEU(): Promise<CountriesList> {
    const path = '/locale/countries/eu';
    const payload: Payload = {};

    return await this.appwriteService.call<CountriesList>('GET', path, {
      'content-type': 'application/json',
    }, payload);
  }


  /**
   * List Countries Phone Codes
   *
   * List of all countries phone codes. You can use the locale header to get the
   * data in a supported language.
   *
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async getCountriesPhones(): Promise<PhonesList> {
    const path = '/locale/countries/phones';
    const payload: Payload = {};


    return await this.appwriteService.call<PhonesList>('GET', path, {
      'content-type': 'application/json',
    }, payload);
  }


  /**
   * List Currencies
   *
   * List of all currencies, including currency symbol, name, plural, and
   * decimal digits for all major and minor currencies. You can use the locale
   * header to get the data in a supported language.
   *
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async getCurrencies(): Promise<CurrenciesList> {
    const path = '/locale/currencies';
    const payload: Payload = {};

    return await this.appwriteService.call<CurrenciesList>('GET', path, {
      'content-type': 'application/json',
    }, payload);
  }


  /**
   * List Languages
   *
   * List of all languages classified by ISO 639-1 including 2-letter code, name
   * in English, and name in the respective language.
   *
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async getLanguages(): Promise<LanguagesList> {
    const path = '/locale/languages';
    const payload: Payload = {};

    return await this.appwriteService.call<LanguagesList>('GET', path, {
      'content-type': 'application/json',
    }, payload);
  }
}
