import { Injectable } from '@angular/core';
import { Document, DocumentsList } from '@appwrite/common';
import { AppwriteException } from '../exceptions';
import { Payload } from '../types';
import { AppwriteService } from './appwrite.service';


@Injectable()
export class AppwriteDocumentsService<T = Document<any>> {

  constructor(
    private appwriteService: AppwriteService,
  ) {
  }


  /**
   * List Documents
   *
   * Get a list of all the user documents. You can use the query params to
   * filter your results. On admin mode, this endpoint will return a list of all
   * of the project's documents. [Learn more about different API
   * modes](/docs/admin).
   *
   * @param {string} collectionId
   * @param {string[]} filters
   * @param {number} limit
   * @param {number} offset
   * @param {string} orderField
   * @param {string} orderType
   * @param {string} orderCast
   * @param {string} search
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async listDocuments<R>(collectionId: string, filters?: string[], limit?: number, offset?: number, orderField?: string, orderType?: string, orderCast?: string, search?: string): Promise<DocumentsList<R>> {
    if (typeof collectionId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "collectionId"');
    }

    const path = '/database/collections/{collectionId}/documents'.replace('{collectionId}', collectionId);
    const payload: Payload = {};

    if (typeof filters !== 'undefined') {
      payload['filters'] = filters;
    }

    if (typeof limit !== 'undefined') {
      payload['limit'] = limit;
    }

    if (typeof offset !== 'undefined') {
      payload['offset'] = offset;
    }

    if (typeof orderField !== 'undefined') {
      payload['orderField'] = orderField;
    }

    if (typeof orderType !== 'undefined') {
      payload['orderType'] = orderType;
    }

    if (typeof orderCast !== 'undefined') {
      payload['orderCast'] = orderCast;
    }

    if (typeof search !== 'undefined') {
      payload['search'] = search;
    }

    return this.appwriteService.call<DocumentsList<R>>(
      'GET',
      path,
      { 'content-type': 'application/json', },
      payload
    );
  }

  /**
   * Create Document
   *
   * Create a new Document. Before using this route, you should create a new
   * collection resource using either a [server
   * integration](/docs/server/database#databaseCreateCollection) API or
   * directly from your database console.
   *
   * @param {string} collectionId
   * @param {object} data
   * @param {string[]} read
   * @param {string[]} write
   * @param {string} parentDocument
   * @param {string} parentProperty
   * @param {string} parentPropertyType
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async createDocument<R>(collectionId: string, data: Record<string, any>, read?: string[], write?: string[], parentDocument?: string, parentProperty?: string, parentPropertyType?: string): Promise<Document<R>> {
    if (typeof collectionId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "collectionId"');
    }

    if (typeof data === 'undefined') {
      throw new AppwriteException('Missing required parameter: "data"');
    }

    const path = '/database/collections/{collectionId}/documents'.replace('{collectionId}', collectionId);
    const payload: Payload = {};

    if (typeof data !== 'undefined') {
      payload['data'] = data;
    }

    if (typeof read !== 'undefined') {
      payload['read'] = read;
    }

    if (typeof write !== 'undefined') {
      payload['write'] = write;
    }

    if (typeof parentDocument !== 'undefined') {
      payload['parentDocument'] = parentDocument;
    }

    if (typeof parentProperty !== 'undefined') {
      payload['parentProperty'] = parentProperty;
    }

    if (typeof parentPropertyType !== 'undefined') {
      payload['parentPropertyType'] = parentPropertyType;
    }

    return this.appwriteService.call<Document<R>>('POST', path, { 'content-type': 'application/json', }, payload);
  }

  /**
   * Get Document
   *
   * Get a document by its unique ID. This endpoint response returns a JSON
   * object with the document data.
   *
   * @param {string} collectionId
   * @param {string} documentId
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async getDocument<R>(collectionId: string, documentId: string): Promise<Document<R>> {
    if (typeof collectionId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "collectionId"');
    }

    if (typeof documentId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "documentId"');
    }

    const path = '/database/collections/{collectionId}/documents/{documentId}'.replace('{collectionId}', collectionId)
      .replace('{documentId}', documentId);
    const payload: Payload = {};

    return this.appwriteService.call<Document<R>>('GET', path, { 'content-type': 'application/json', }, payload);
  }


  /**
   * Update Document
   *
   * Update a document by its unique ID. Using the patch method you can pass
   * only specific fields that will get updated.
   *
   * @param {string} collectionId
   * @param {string} documentId
   * @param {object} data
   * @param {string[]} read
   * @param {string[]} write
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async updateDocument<R>(collectionId: string, documentId: string, data: any, read?: string[], write?: string[]): Promise<Document<R>> {
    if (typeof collectionId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "collectionId"');
    }

    if (typeof documentId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "documentId"');
    }

    if (typeof data === 'undefined') {
      throw new AppwriteException('Missing required parameter: "data"');
    }

    const path = '/database/collections/{collectionId}/documents/{documentId}'.replace('{collectionId}', collectionId)
      .replace('{documentId}', documentId);
    const payload: Payload = {};

    if (typeof data !== 'undefined') {
      payload['data'] = data;
    }

    if (typeof read !== 'undefined') {
      payload['read'] = read;
    }

    if (typeof write !== 'undefined') {
      payload['write'] = write;
    }
    return this.appwriteService.call<Document<R>>('PATCH', path, { 'content-type': 'application/json', }, payload);
  }

  /**
   * Delete Document
   *
   * Delete a document by its unique ID. This endpoint deletes only the parent
   * documents, its attributes and relations to other documents. Child documents
   * **will not** be deleted.
   *
   * @param {string} collectionId
   * @param {string} documentId
   * @throws {AppwriteException}
   * @returns {Promise}
   */
  async deleteDocument(collectionId: string, documentId: string): Promise<void> {
    if (typeof collectionId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "collectionId"');
    }

    if (typeof documentId === 'undefined') {
      throw new AppwriteException('Missing required parameter: "documentId"');
    }

    const path = '/database/collections/{collectionId}/documents/{documentId}'.replace('{collectionId}', collectionId)
      .replace('{documentId}', documentId);
    const payload: Payload = {};

    return this.appwriteService.call<void>('DELETE', path, { 'content-type': 'application/json', }, payload);

  }

}
